import { GameObject } from "./Objects/objects.module";
import { Random } from "./Utils/Random";
import { Point } from "./Drawing/Graphics";

class Area {
    constructor(readonly x: number,
        readonly y: number,
        readonly width: number,
        readonly height: number) {

    }
}

class Spawner {

    public randomRespawn(object: GameObject, area: Area, minDistance = 250): void {
        let leftBoundary = area.width - object.width;
        let bottomBoundary = area.height - object.height;

        let distance, newX, newY: number = 0;
        do {
            newX = Random.FromInterval(area.x, leftBoundary);
            newY = Random.FromInterval(area.y, bottomBoundary);
            let newPos = new Point(newX, newY);

            distance = object.middlePoint.distance(newPos);
        } while (distance < minDistance);

        object.x = newX;
        object.y = newY;
    }
}

export {
    Area,
    Spawner
}